* {
    margin: 0;
    padding: 0;
}

html, body {
    height: 100%;
    width: 100%;
}

#root {
    height: inherit;
    width: inherit;
}

.title {
    color: rgb(108, 223, 185);
}

.subtitle {
    color: rgb(107, 238, 236);
}

.landing-logo {
    position: fixed;
    left: 680px;
    top: 30px;
    width: 600px;
}

.form-wrapper{
    display: contents;
}

.form-elem{
    margin-top: 20px;
}

.text-input{
    font-size: 15px;
    margin: 10px;
    border-radius: 10px;
    padding: 5px;
}

.text-muted{
    font-family: sans-serif;
    color: #4f8ebd;
    font-size: 25px;
}

.form-body {
    margin-top: 10px;
}

.form-label {
    font-family: 'Georgia';
    color: #1693ff;
    font-size: 25px;
}

.App {
    background: linear-gradient(180deg, #582680, #010c42, #0e8989);
    background-size: inherit;
    height: inherit;
    width: inherit;
};